
body {
  font-family: "Source Sans Pro", sans-serif;
  padding: 0;
  margin: 0;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}


.btn {
  padding: 10px 15px;
  background-color: #21d07a;
  color: #032541;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  border: none;
  font-size: 1rem;
  transition: all 0.3s ease;
  line-height: 1.1;
}

.btn:hover {
  background-color: #1aa762;
  opacity: 1;
  cursor: pointer;
}

.btn:disabled {
  pointer-events: none;
  opacity: 0.5;
}

input:focus {
  outline: none;
}

/* Header Styles */
header {
  background-color: darkgray;
}

header .inner-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}

header a {
  color: #fff;
  font-size: 1.25rem;
  text-decoration: none;
}

header a:hover {
  opacity: 0.7;
}

header .brand a {
  font-weight: 700;
  font-size: 1.5rem;
}


header .nav-links li {
  display: inline-block;
  margin-right: 30px;
}

.heading{
  width: 100%;
  text-align: center;
  color: #032541;
  margin: 40px;
}

/* Search Page */
.add-content {
  padding: 50px 0;
  max-width: 800px;
  margin: 0 auto;
}

.add-content .input-wrapper input {
  width: 100%;
  background-color: dimgray;
  padding: 10px;
  border-radius: 20px;
  border: none;
  color: #fff;
  font-size: 1.25rem;
}
.add-content .input-wrapper input::placeholder{
  color: rgba(0, 0, 0, 0.8);
}

.results {
  padding: 0;
  margin: 0;
  margin-top: 40px;
}

.results li {
  list-style: none;
}

.result-card {
  display: flex;
  margin-bottom: 20px;
}

.result-card .poster-wrapper img,
.filler-poster {
  width: 100px;
  height: 150px;
  border-radius: 5px;
  margin-right: 15px;
  display: block;
}

.result-card .info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.result-card .info .title {
  font-size: 1.25rem;
  margin: 0;
}

.result-card .info .release-date {
  font-size: 1.25rem;
  font-weight: 300;
  color: #032541;
  margin: 0;
}

.result-card .controls button {
  margin-right: 10px;
}

/* Fav Page */

.movie-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
}

@media (min-width: 992px) {
  .movie-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.movie-card {
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  border: 0;
  display: flex;
  margin-bottom: 20px;
  margin-left: 10%;
}

.movie-card img {
  width: 100%;
  
}
.desc {
  width: 100%;
}

.movie-card .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.movie-card:hover .overlay {
  border: 3px solid red;
  
}

.movie-card .inner-card-controls {
  position: absolute;
  bottom: 20px;
  left: 45%;
  border-radius: 5px;
  padding-left: 6px;
  padding-right: 6px;
  opacity: 0;
  transition: all 0.3s ease;
  background-color: darkgray;
}

.movie-card:hover .inner-card-controls {
  opacity: 1;
}

.ctrl-btn {
  color: #fefefe;
  background-color: transparent;
  border: none;
  transition: all 0.3s ease;
  font-size: 1.25rem;
  padding: 5px;
  margin: 0;
}

.ctrl-btn:hover {
  color: red;
  cursor: pointer;
  font-size: 30px;
}

.no-movies {
  color: #c4c4c4;
  font-size: 3rem;
  text-align: center;
}
